/* wrapper */
/* column */
.v-media-image {
  width: 100%;
  height: 100%;
  /*transition: opacity 250ms ease-in-out;*/
  opacity: 0;
}
.v-media-image__img {
    height: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    margin: auto;
    width: 100%;
}
@media (min-width: 920px) {
.v-media-image__img {
        width: 85%;
}
}
@media (min-width: 1060px) {
.v-media-image__img {
        width: 100%;
}
}
@media (max-width: 779px) {
.v-media-image:first-child .v-media-image__img > * {
      object-fit: cover;
}
}
.v-media-image__copyright {
    margin-top: 0.2em;
}
.v-media-image__description {
    margin-top: 1.2em;
}
.v-media-image.is-visible {
  opacity: 1;
}
