a,
.mmd-link {
  color: inherit;
  text-decoration: none;
  overflow-wrap: break-word;
  background-repeat: repeat-x;
  background-image: linear-gradient(to right, currentColor 100%, currentColor 0);
  background-size: 1px 1px;
  background-position: 0 100%;
}
a.mmd-link--arrow,
  .mmd-link.mmd-link--arrow {
    text-decoration: none;
    background-image: none;
}
a.mmd-link--arrow:after,
    .mmd-link.mmd-link--arrow:after {
      content: ' \2192';
}
a.mmd-link--no-underline,
  .mmd-link.mmd-link--no-underline {
    background-image: none;
}
.v-app-footer {
  background-color: white;
  color: black;
}
.v-app-footer.bg-dark {
    background-color: black;
    color: white;
}
.v-app-footer__wrap {
  font-size: 15px;
  line-height: 18px;
  font-family: "Klim Domaine Sans Text", sans-serif;
  font-weight: 400;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: stretch;
  position: relative;
  box-sizing: border-box;
  justify-content: space-between;
}
.v-app-footer__wrap > *:first-child {
    margin-top: 0;
}
.v-app-footer__wrap > *:last-child {
    margin-bottom: 0;
}
.v-app-footer__wrap > * {
    flex: none;
}
.v-app-footer__left,
.v-app-footer__center {
  padding-left: 18px;
  padding-right: 18px;
  display: block;
  position: relative;
  box-sizing: border-box;
  width: 100%;
}
.v-app-footer__left > *:first-child,
  .v-app-footer__center > *:first-child {
    margin-top: 0;
}
.v-app-footer__left > *:last-child,
  .v-app-footer__center > *:last-child {
    margin-bottom: 0;
}
@media (min-width: 780px) {
.v-app-footer__left,
    .v-app-footer__center {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 50%;
}
.v-app-footer__left > *:first-child,
      .v-app-footer__center > *:first-child {
        margin-top: 0;
}
.v-app-footer__left > *:last-child,
      .v-app-footer__center > *:last-child {
        margin-bottom: 0;
}
}
@media (min-width: 1060px) {
.v-app-footer__left,
    .v-app-footer__center {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 40%;
}
.v-app-footer__left > *:first-child,
      .v-app-footer__center > *:first-child {
        margin-top: 0;
}
.v-app-footer__left > *:last-child,
      .v-app-footer__center > *:last-child {
        margin-bottom: 0;
}
}
.v-app-footer__center {
  margin-top: 2rem;
}
@media (min-width: 920px) {
.v-app-footer__center {
      margin-top: 0;
}
}
.v-app-footer__right {
  padding-left: 18px;
  padding-right: 18px;
  display: block;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  margin-top: 2rem;
}
.v-app-footer__right > *:first-child {
    margin-top: 0;
}
.v-app-footer__right > *:last-child {
    margin-bottom: 0;
}
@media (min-width: 780px) {
.v-app-footer__right {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 50%;
      text-align: right;
      margin-top: 0;
}
.v-app-footer__right > *:first-child {
        margin-top: 0;
}
.v-app-footer__right > *:last-child {
        margin-bottom: 0;
}
}
