/* grid */
#image-viewer {
  position: fixed;
  background: black;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000000;
  transform: translate3d(0px, 0px, 20000px);
}
#app .app-cube-container {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  transform-style: preserve-3d;
  transform: rotateX(0deg) rotateY(0deg);
  transition: transform 1000ms ease-in-out;
}
#app .app-cube-container.app-current-home {
    transform: rotateY(0deg);
}
#app .app-cube-container.app-current-project {
    transform: rotateY(-90deg);
}
#app .app-cube-container.app-current-thesis {
    transform: rotateY(90deg);
}
#app .app-cube-container.app-current-alumni {
    transform: rotateX(-90deg);
}
#app .app-cube-container.app-current-contact {
    transform: rotateX(90deg);
}
#app [class*=app-cube-flip] {
  display: none;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
#app [class*=app-cube-flip].app-page-current {
    display: block;
}
#app [class*=app-cube-flip].app-cube-flip-home {
    background-color: black;
}
#app [class*=app-cube-flip].app-cube-flip-project {
    background-color: black;
}
#app [class*=app-cube-flip].app-cube-flip-thesis {
    background-color: black;
}
#app [class*=app-cube-flip].app-cube-flip-alumni {
    background-color: white;
}
#app [class*=app-cube-flip].app-cube-flip-contact {
    background-color: white;
    scroll-behavior: smooth;
}
#app .page-transition-playing.app-cube-container {
  overflow: visible;
  position: absolute;
  width: 0;
  height: 0;
  top: 50vh;
  left: 50vw;
}
#app .page-transition-playing [class*=app-cube-flip] {
  display: block;
  position: absolute;
  top: -50vh;
  left: -50vw;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  overflow: hidden;
}
#app .page-transition-playing .app-cube-flip-home {
  /*transform: rotateY(0deg);*/
  /*will-change: transform;*/
}
#app .page-transition-playing .app-cube-flip-projects {
  transform: rotateY(90deg);
}
#app .page-transition-playing .app-cube-flip-thesis {
  transform: rotateY(-90deg);
}
#app .page-transition-playing .app-cube-flip-alumni {
  transform: rotateX(90deg);
}
#app .page-transition-playing .app-cube-flip-contact {
  transform: rotateX(-90deg);
}
