button,
.button {
  font-family: "Klim Domaine Sans Text", sans-serif;
  background: none;
  color: white;
  font-size: 2em;
  line-height: 1rem;
  padding: calc(1rem - 1px);
  border: solid 1px;
  box-sizing: border-box;
  display: inline-block;
  cursor: pointer;
}
button.button--small,
  .button.button--small {
    font-size: 1.2em;
    padding: calc(.5rem - 1px);
}
button:disabled,
  .button:disabled {
    border-color: transparent;
}
button.button--revert,
  .button.button--revert {
    background: white;
    color: black;
}
button.has-no-border,
  .button.has-no-border {
    border: none;
}
button.button--arrow:after,
  .button.button--arrow:after {
    content: ' \2192';
}
.mmd-buttons button + button, .mmd-buttons
  .button + button, .mmd-buttons button +
  .button, .mmd-buttons
  .button +
  .button {
    margin-left: 18px;
}
@media (min-width: 550px) {
.mmd-buttons button + button, .mmd-buttons
      .button + button, .mmd-buttons button +
      .button, .mmd-buttons
      .button +
      .button {
        margin-left: 36px;
}
}
.mmd-buttons {
  display: flex;
}
