.v-projects-section {
  margin-top: 10rem;
  margin-bottom: 10rem;
}
@media (min-width: 550px) {
.v-projects-section {
      margin-top: 17rem;
      margin-bottom: 14rem;
}
}
.v-projects-section__header {
    font-size: 15px;
    line-height: 18px;
    font-family: "Klim Domaine Sans Text", sans-serif;
    font-weight: 400;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    box-sizing: border-box;
    margin-bottom: 4rem;
}
.v-projects-section__header > *:first-child {
      margin-top: 0;
}
.v-projects-section__header > *:last-child {
      margin-bottom: 0;
}
.v-projects-section__header > * {
      flex: none;
}
@media (min-width: 550px) {
.v-projects-section__header {
        margin-bottom: 8rem;
}
}
.v-projects-section__header__top {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 100%;
}
.v-projects-section__header__top > *:first-child {
        margin-top: 0;
}
.v-projects-section__header__top > *:last-child {
        margin-bottom: 0;
}
.v-projects-section__header__top > *:first-child {
        margin-top: 0;
}
.v-projects-section__header__top > *:last-child {
        margin-bottom: 0;
}
.v-projects-section__content {
    font-size: 15px;
    line-height: 18px;
    font-family: "Klim Domaine Sans Text", sans-serif;
    font-weight: 400;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    box-sizing: border-box;
    margin-top: 4em;
}
.v-projects-section__content > *:first-child {
      margin-top: 0;
}
.v-projects-section__content > *:last-child {
      margin-bottom: 0;
}
.v-projects-section__content > * {
      flex: none;
}
.v-projects-section__content__projects {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 100%;
}
.v-projects-section__content__projects > *:first-child {
        margin-top: 0;
}
.v-projects-section__content__projects > *:last-child {
        margin-bottom: 0;
}
.v-projects-section__content__projects > *:first-child {
        margin-top: 0;
}
.v-projects-section__content__projects > *:last-child {
        margin-bottom: 0;
}
.v-projects-section__title {
  font-family: "Suisse Neue", serif;
  font-weight: 800;
  font-size: 2.4em;
  line-height: 2rem;
  padding-top: 6px;
  padding-bottom: 12px;
  margin-top: 5rem;
  margin-bottom: 3rem;
}
.v-projects-section__title.has-no-space-next-line:not(:last-child) {
    padding-bottom: 0;
}
.v-projects-section__title.has-no-space-next-line + * {
    padding-top: 0;
}
.v-projects-section__title strong {
    font-weight: 900;
}
@media (min-width: 550px) {
.v-projects-section__title {
      font-size: 4em;
      line-height: 4rem;
      padding-top: 16px;
      padding-bottom: 3px;
}
}
@media (min-width: 780px) {
.v-projects-section__title {
      font-size: 6.5em;
      padding-top: 4px;
      line-height: 6rem;
      padding-bottom: 14px;
}
}
@media (min-width: 920px) {
.v-projects-section__title {
      font-size: 8em;
      line-height: 8rem;
      padding-top: 12px;
      padding-bottom: 6px;
}
}
