#bottom-bar {
  transform: translate3d(0px, 0px, 20000px);
  position: fixed;
  left: 0;
  bottom: 0;
  color: blue;
  background-color: white;
  box-sizing: border-box;
  padding-right: 4rem;
  width: 100%;
}
#bottom-bar > *:first-child {
    margin-top: 0;
}
#bottom-bar > *:last-child {
    margin-bottom: 0;
}
#bottom-bar.no-content {
    padding-right: 2rem;
}
#bottom-bar .text-container {
    font-size: 15px;
    line-height: 18px;
    font-family: "Klim Domaine Sans Text", sans-serif;
    font-weight: 400;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 4rem;
}
#bottom-bar .text-container > *:first-child {
      margin-top: 0;
}
#bottom-bar .text-container > *:last-child {
      margin-bottom: 0;
}
#bottom-bar .text-container > * {
      flex: none;
}
#bottom-bar .text-container__scrolling-container {
    height: 2rem;
}
#bottom-bar .text-container__scrolling-container > *:first-child {
      margin-top: 0;
}
#bottom-bar .text-container__scrolling-container > *:last-child {
      margin-bottom: 0;
}
#bottom-bar .text-container__scrolling-container:nth-child(2n) .text-container__text {
      animation-direction: reverse;
}
#bottom-bar .text-container__text {
    font-size: 15px;
    line-height: 18px;
    font-family: "Klim Domaine Sans Text", sans-serif;
    font-weight: 400;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    box-sizing: border-box;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    height: 2rem;
    line-height: 2rem;
    white-space: nowrap;
    position: absolute;
    left: 100%;
    padding-right: 100%;
    animation-duration: 41.41s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-name: text-horizontal-scrolling;
    font-size: 1.2em;
}
#bottom-bar .text-container__text > *:first-child {
      margin-top: 0;
}
#bottom-bar .text-container__text > *:last-child {
      margin-bottom: 0;
}
#bottom-bar .text-container__text > * {
      flex: none;
}
#bottom-bar .text-container__text > *:first-child {
      margin-top: 0;
}
#bottom-bar .text-container__text > *:last-child {
      margin-bottom: 0;
}
#bottom-bar .text-container__text > * {
      margin: 0 !important;
      margin-right: 18px !important;
      padding: 0;
      line-height: 2rem;
      height: 1.5rem;
      font-size: 1em;
}
#bottom-bar .text-container__text > a {
      margin-left: 18px !important;
}
#bottom-bar .text-container__text br {
      content: ' ';
}
#bottom-bar .text-container__text br:after {
        content: ' ';
}
#bottom-bar .text-container__text figure {
      position: relative;
      width: 3.55556rem;
      height: 2rem;
}
#bottom-bar .text-container__text figure > img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
}
@keyframes text-horizontal-scrolling {
from {
    transform: translate3d(0, 0, 0);
}
to {
    transform: translate3d(-100%, 0, 0);
}
}
#bottom-bar .social-container {
    font-size: 15px;
    line-height: 18px;
    font-family: "Klim Domaine Sans Text", sans-serif;
    font-weight: 400;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    box-sizing: border-box;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 2rem;
    max-width: 1280px;
    margin: auto;
    padding-left: 9px;
    padding-right: 9px;
}
#bottom-bar .social-container > *:first-child {
      margin-top: 0;
}
#bottom-bar .social-container > *:last-child {
      margin-bottom: 0;
}
#bottom-bar .social-container > * {
      flex: none;
}
@media (min-width: 1700px) {
#bottom-bar .social-container {
        justify-content: space-between;
}
}
#bottom-bar .social-container__item {
    padding-left: 18px;
    padding-right: 18px;
}
#bottom-bar .social-container__item > * {
      font-family: "Klim Domaine Sans Text", sans-serif;
      font-size: 1.2em;
      font-weight: 400;
      line-height: 1rem;
      padding-top: 4px;
      padding-bottom: 14px;
      margin-top: 0;
      margin-bottom: 0;
}
#bottom-bar .social-container__item > *.has-no-space-next-line:not(:last-child) {
        padding-bottom: 0;
}
#bottom-bar .social-container__item > *.has-no-space-next-line + * {
        padding-top: 0;
}
#bottom-bar .social-container__item > * strong {
        font-weight: 500;
}
#bottom-bar #twitter,
  #bottom-bar #github {
    display: none;
}
@media (min-width: 780px) {
#bottom-bar #twitter,
      #bottom-bar #github {
        display: block;
}
}
#bottom-bar #bottom-bar-btn {
    padding-left: 18px;
    padding-right: 18px;
    padding-top: 18px;
    padding-bottom: 18px;
    background-color: white;
    position: absolute;
    box-sizing: border-box;
    top: 0;
    right: 0;
    cursor: pointer;
    width: 4rem;
    height: 4rem;
}
#bottom-bar #bottom-bar-btn__icon {
    fill: none;
    stroke: black;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
#bottom-bar.no-content #bottom-bar-btn {
    width: 2rem;
    height: 2rem;
    padding: 9px;
}
