/* wrapper */
/* column */
.app-header {
  position: relative;
  perspective: 1500px;
  width: 100%;
  height: auto;
  overflow: hidden;
}
@media (min-width: 780px) {
.app-header {
      width: 100vw;
      height: 100vh;
}
}
.app-header__background-container {
  top: 0;
  left: 0;
  width: 100%;
  background-color: black;
  transform-style: preserve-3d;
  transform-box: view-box;
  height: auto;
  position: relative;
}
@media (min-width: 780px) {
.app-header__background-container {
      height: 100%;
      position: absolute;
      transform: translateZ(-50px);
}
}
.app-header__image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 80%;
  width: calc(100vw + 2rem);
  height: 50vh;
  position: relative;
  top: 0;
  left: 0;
  transform: none;
}
@media (min-width: 780px) {
.app-header__image {
      height: calc(80vh - 2rem);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
}
}
@media (min-width: 920px) {
.app-header__image {
      width: calc(100vw - 2rem);
      height: calc(100vh - 2rem);
}
}
@media (min-width: 1060px) {
.app-header__image {
      width: calc(100vw - 5rem);
      height: calc(100vh - 5rem);
}
}
.app-header__text-container {
  width: 100%;
  pointer-events: none !important;
  height: auto;
  position: relative;
  margin-top: 4rem;
}
@media (min-width: 780px) {
.app-header__text-container {
      height: 95%;
      position: relative;
      margin-top: 0;
}
}
@media (min-width: 920px) {
.app-header__text-container {
      height: 100%;
}
}
.app-header__text {
  padding-left: 18px;
  padding-right: 18px;
  mix-blend-mode: difference;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  bottom: 0.2rem;
}
@media (min-width: 780px) {
.app-header__text {
      position: absolute;
      bottom: 3rem;
}
}
.app-header__text > * {
    margin: 0;
}

/* fonts */
/* colors */
/* grid */
.header-with-text__text {
  width: calc(100% / 12 * 10);
  position: absolute;
  left: 50%;
  top: 33%;
  transform: translateX(-50%) translateY(-33%) rotateX(33deg) rotateY(23deg) rotateZ(-19deg);
}
.header-with-text__text:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 4em;
    left: 0;
    bottom: -1px;
    background: linear-gradient(rgba(0, 0, 0, 0), black);
}
