.v-cartel__description {
  overflow: hidden;
  transition: max-height 500ms ease-in-out;
  max-height: 0;
}
.v-cartel__description.show-details {
    max-height: 50em;
    margin-top: 0;
    margin-bottom: 1rem;
}
@media (min-width: 550px) {
.v-cartel__description.show-details {
        margin-top: 2rem;
        margin-bottom: 2rem;
}
}
.v-cartel__description.show-details:after {
      opacity: 0;
}
.v-cartel__description.no-max-height {
    max-height: none !important;
}
.v-cartel__description:not(.show-details) + .mmd-buttons {
    margin-top: 2rem;
}
@media (min-width: 920px) {
.v-cartel__description:not(.show-details) + .mmd-buttons {
        margin-top: 4rem;
}
}
.v-cartel__description:after {
    content: "";
    display: block;
    width: 100%;
    height: 4em;
    background: linear-gradient(rgba(0, 0, 0, 0), black);
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    transition: opacity 500ms cubic-bezier(0, 0, 0.2, 1);
    opacity: 1;
}
.v-cartel__header + .mmd-buttons {
  margin-top: 1rem;
}
.v-cartel__header__title {
  max-width: 12em;
}
@media (max-width: 549px) {
.v-cartel__header__title {
      font-size: 2em;
      margin-top: 3rem;
      margin-bottom: 1rem;
}
}
.v-project__header__text-container {
  max-width: 1066px;
  margin: auto;
  display: block;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding-left: 18px;
  padding-right: 18px;
}
.v-project__header__text-container > *:first-child {
    margin-top: 0;
}
.v-project__header__text-container > *:last-child {
    margin-bottom: 0;
}
@media (min-width: 920px) {
.v-project__header__text-container {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 91.66667%;
}
.v-project__header__text-container > *:first-child {
        margin-top: 0;
}
.v-project__header__text-container > *:last-child {
        margin-bottom: 0;
}
}
@media (min-width: 1260px) {
.v-project__header__text-container {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 83.33333%;
}
.v-project__header__text-container > *:first-child {
        margin-top: 0;
}
.v-project__header__text-container > *:last-child {
        margin-bottom: 0;
}
}
.v-cartel__header__author {
  font-family: "Klim Domaine Sans Text", sans-serif;
  font-size: 1.2em;
  font-weight: 400;
  line-height: 1rem;
  padding-top: 4px;
  padding-bottom: 14px;
  margin-top: 0;
  margin-bottom: 0;
}
.v-cartel__header__author.has-no-space-next-line:not(:last-child) {
    padding-bottom: 0;
}
.v-cartel__header__author.has-no-space-next-line + * {
    padding-top: 0;
}
.v-cartel__header__author strong {
    font-weight: 500;
}
.v-cartel__description {
  font-size: 15px;
  line-height: 18px;
  font-family: "Klim Domaine Sans Text", sans-serif;
  font-weight: 400;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: stretch;
  position: relative;
  box-sizing: border-box;
}
.v-cartel__description > *:first-child {
    margin-top: 0;
}
.v-cartel__description > *:last-child {
    margin-bottom: 0;
}
.v-cartel__description > * {
    flex: none;
}
.v-cartel__description > *:first-child {
    margin-top: 0;
}
.v-cartel__description > *:last-child {
    margin-bottom: 0;
}
.v-cartel__description__texts {
  display: block;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 1rem;
}
.v-cartel__description__texts > *:first-child {
    margin-top: 0;
}
.v-cartel__description__texts > *:last-child {
    margin-bottom: 0;
}
@media (min-width: 780px) {
.v-cartel__description__texts {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 87.5%;
      margin-left: 8.33333%;
}
.v-cartel__description__texts > *:first-child {
        margin-top: 0;
}
.v-cartel__description__texts > *:last-child {
        margin-bottom: 0;
}
}
@media (min-width: 920px) {
.v-cartel__description__texts {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 91.66667%;
      margin-left: 0%;
}
.v-cartel__description__texts > *:first-child {
        margin-top: 0;
}
.v-cartel__description__texts > *:last-child {
        margin-bottom: 0;
}
}
@media (min-width: 1060px) {
.v-cartel__description__texts {
      display: block;
      position: relative;
      box-sizing: border-box;
      width: 83.33333%;
}
.v-cartel__description__texts > *:first-child {
        margin-top: 0;
}
.v-cartel__description__texts > *:last-child {
        margin-bottom: 0;
}
}
@media (min-width: 780px) {
.v-cartel__description__images {
    padding-left: 8.33333%;
}
}
@media (min-width: 920px) {
.v-cartel__description__images {
    padding-left: 0;
}
}
@media (min-width: 780px) {
.v-cartel__buttons {
    padding-left: 8.33333%;
}
}
@media (min-width: 920px) {
.v-cartel__buttons {
    padding-left: 0;
}
}
.v-project {
  position: relative;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.v-project > *:first-child {
    margin-top: 0;
}
.v-project > *:last-child {
    margin-bottom: 0;
}
@media (min-width: 550px) {
.v-project {
      margin-bottom: 10rem;
}
}
.v-project__header {
    position: relative;
    height: auto;
}
@media (min-width: 920px) {
.v-project__header {
        height: 70vh;
}
}
.v-project__header__images {
      position: relative;
      height: 20rem;
}
@media (min-width: 920px) {
.v-project__header__images {
          height: 100%;
}
}
@media (max-width: 779px) {
.v-project__header__images {
          transform: none !important;
}
}
.v-project__header__images .v-media-image {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100vw;
}
.v-project__header__texts {
      left: 0;
      mix-blend-mode: difference;
      width: 100%;
      position: relative;
      top: -2rem;
      margin-bottom: -2rem;
}
@media (max-width: 779px) {
.v-project__header__texts {
          transform: none !important;
}
}
@media (min-width: 780px) {
.v-project__header__texts {
          transform: translateY(-50%);
          position: absolute;
          top: auto;
          bottom: -5rem;
          margin-bottom: 0;
          padding-left: 8.33333%;
}
}
@media (min-width: 920px) {
.v-project__header__texts {
          padding-left: 0;
          top: 50%;
          bottom: auto;
}
}
.v-project__header {
  perspective: 2500px;
  cursor: e-resize;
  width: 100vw;
  margin: auto;
  transform: translateX(-50%);
  left: 50%;
  overflow: hidden;
}
.v-project__header.to-left {
    cursor: w-resize;
}
